<template>
  <!-- Â∞ÅË£ÖÂºπÊ°Ü -->
  <div class="popup">
    <el-dialog
      :title="dialogTitle"
      :visible.sync="dialogVisible"
      :width="popupWidth"
      :before-close="handleClose"
      :append-to-body="true"
    >
      <slot>
        <p>ÂºπÊ°ÜËá™ÂÆö‰πâÁöÑÂÜÖÂÆπ</p>
      </slot>
      <span slot="footer" class="dialog-footer">
        <!-- <el-button @click="Cancel">Âèñ Ê∂à</el-button> -->
        <el-button type="primary" @click="Save">Á°Æ ÂÆö</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "dialogView",
  props: {
    dialogTitle: {
      type: String,
      default: "Ê†áÈ¢ò",
    },
    visible: {
      type: Boolean,
      default: false,
    },
    popupWidth: {
      type: String,
      default: "550px",
    },
  },
  computed: {
    dialogVisible: {
      get() {
        return this.visible;
      },
      set(val) {
        // ÂΩìvisibleÊîπÂèòÁöÑÊó∂ÂÄôÔºåËß¶ÂèëÁà∂ÁªÑ‰ª∂ÁöÑ updateVisibleÊñπÊ≥ïÔºåÂú®ËØ•ÊñπÊ≥ï‰∏≠Êõ¥Êîπ‰º†ÂÖ•Â≠êÁªÑ‰ª∂ÁöÑ centerDialogVisibleÁöÑÂÄº
        this.$emit("updateVisible", val);
      },
    },
  },
  methods: {
    Cancel() {
      this.$emit("resetPopupData");
    },
    Save() {
      this.$emit("submitPopupData");
    },
    handleClose() {
      this.$emit("handleClose");
    },
  },
};
</script>
